.actionIcon__button{
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    margin-bottom:0.5px;
    transition:0.1s;
}

.actionIcon__button:hover{
    transform:scale(0.9);
    cursor:pointer;
}

.action__iconContainer{
    width:70px;
    height:70px;
    display:flex;
    justify-content:center;
    align-items:center;
}

.action__icon{
    display:flex;
    justify-content:center;
    align-items:center;
    width:55px;
    height:55px;
    border-radius: 50%;
}

.action__name{
    text-align: center;
    font-size: 0.8em;
    font-weight: bold;
}

@media (max-width:480px){
    .action__name{
        font-size: 0.7em;
    }
}

@media (max-width:411px){
    .action__name{
        font-size: 0.65em;
    }
}