@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap);
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}

body{
  font-family: 'Inter', sans-serif;
}

a{
  text-decoration:none;
  color:black;
}
.main__container{
    display:flex;
    width:100%;
    justify-content:center;
}

.main__container > * {
    margin-top: 1em;
}
.navbar{
    height:65px;
    display:flex;
    width:100%;
    justify-content:center;
    align-items:center;
}

.navbar-logo{
    display:flex;
    align-items: center;
    width: 1000px;
}

.navbar-logo > * {
    margin-left: 5em;
}

@media (max-width: 411px) {
    .navbar-logo > * {
        margin-left: 2em;
    }
}

.card__container{
    height: 200px;
    width: 250px;
    background-color: white;
    margin-bottom: 2em;
    background-size:cover;
    overflow: hidden;
}

.card_categoryImageContainer{
    height: 200px;
    width: 250px;
    background-size:cover;
    position: relative;
}

.card__categoryName{
    position: absolute;
    width: 80%;
    text-align: center;
    left:10%;
    top:80%;
    border-radius: 9px;
    font-weight: bolder;
    background-color:#FFF;
}

@media (max-width:411px){
    .card__container{
        width:230px;
    }
}
.container{
    width:1000px;
    display:flex;
    justify-content:center;
    flex-wrap:wrap;
}

.container__title{
    flex-basis:100%;
    display:flex;
    justify-content:center;
    margin-bottom:1em;
}

.container > * {
    margin-left: 5em;
}

@media (max-width: 685px) {
    .container > * {
        margin-left: 6em;
    }
}

@media (max-width: 540px) {
    .container > * {
        margin-left: 3em;
    }
}

@media (max-width: 411px) {
    .container > * {
        margin-left: 0em;
    }
}
.actionIcon__button{
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    margin-bottom:0.5px;
    -webkit-transition:0.1s;
    transition:0.1s;
}

.actionIcon__button:hover{
    -webkit-transform:scale(0.9);
            transform:scale(0.9);
    cursor:pointer;
}

.action__iconContainer{
    width:70px;
    height:70px;
    display:flex;
    justify-content:center;
    align-items:center;
}

.action__icon{
    display:flex;
    justify-content:center;
    align-items:center;
    width:55px;
    height:55px;
    border-radius: 50%;
}

.action__name{
    text-align: center;
    font-size: 0.8em;
    font-weight: bold;
}

@media (max-width:480px){
    .action__name{
        font-size: 0.7em;
    }
}

@media (max-width:411px){
    .action__name{
        font-size: 0.65em;
    }
}
.action__buttonContainer{
    margin-top:0.5em;
    height:70px;
    display:flex;
    justify-content:space-around;
}
.catAction__container{
    width:350px;
    height:425px;
    border-radius: 10px;
    border: 0.1px solid grey;
}

.catAction__imageContainer{
    height:320px;
    width:100%;
    border-radius: 10px 10px 0 0;
    background-size:cover;
}

@media (max-width: 480px){
    .catAction__container{
        width:285px;
        margin-left:3.9em;
    }
}

@media (max-width: 411px){
    .catAction__container{
        width:240px;
        margin-left:1em;
    }
}

.catAction__Count{
    text-align:center;
    font-size:small;
    margin-top:5px;
}
.container__categoryTitle{
    justify-content:flex-start;
    margin-left: 5.3em;
}

.cat__actionQuestion{
    flex-basis: 100%;
    text-align: center;
    margin-bottom:0.5em;
}

@media (max-width: 411px) {
    .container__categoryTitle{
        margin-left: 2.3em;
    }
}
.stats__box{
    border: 0.1px solid rgb(243, 233, 233);
    height:70px;
    width:280px;
    display: flex;
    justify-content:space-between;
    padding: 0 10px;
    margin-right:1em;
    margin-bottom:1em;
}

.stats__info{
    display: flex;
    flex-direction:column;
    justify-content:center;
}

.stats__count{
    text-align:end;
    font-weight: bold;
    font-size: x-large;
}

.stats__actionText{
    font-size: small;
}

@media (max-width:411){
    .stats__box{
        width:265px;
    }
}
.statsBox__container{
    width:100%;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left:3em;
}

@media (max-width: 1000px) {
    .statsBox__container{
        margin-left:5.2em;
    }
}

@media (max-width: 685px) {
    .statsBox__container{
        margin-left:5.4em;
    }
}

@media (max-width: 411px) {
    .statsBox__container{
        margin-left:2.5em;
    }
}
.stats__button{
    margin-top:2em;
    height:50px;
    width:280px;
    padding:0 1em;
    cursor: pointer;
    display:flex;
    justify-content:center;
    align-items:center;
    border-radius: 5px;
    -webkit-transition:0.1s;
    transition:0.1s;
}

.stats__button:hover{
    -webkit-transform:scale(0.9);
            transform:scale(0.9);
}

.stats__buttonText{
    font-size:1em;
}

@media (max-width: 679px) {
    .stats__button{
        margin-top:1em;
        margin-bottom:1em;
        height:40px;
    }
    .stats__buttonText{
        font-size:0.9em;
    }
}
.userAction_buttonContainer{
    margin-left:2em;
}

@media (min-width: 411px) and (max-width: 1000px) {
    .userAction_buttonContainer{
        margin-left:4.5em;
    }
}

@media (max-width: 411px) {
    .userAction_buttonContainer{
        margin-left:1.5em;
    }
}
.stats__text{
    justify-content:flex-start;
    font-weight: 700;
    font-size:10px;
    margin-left:8.2em;
    font-family: 'Inter', sans-serif;
}

.catsSeen__text{
    justify-content:flex-start;
    font-weight: 550;
    font-size:12px;
    margin-left:6.8em;
}

@media (max-width: 685px) {
    .stats__text{
        margin-left: 8.3em;
    }
    .catsSeen__text{
        margin-left:7em;
    }
}

@media (max-width: 411px) {
    .stats__text{
        margin-left: 4em;
    }
    .catsSeen__text{
        margin-left:3.4em;
    }
}
