.stats__button{
    margin-top:2em;
    height:50px;
    width:280px;
    padding:0 1em;
    cursor: pointer;
    display:flex;
    justify-content:center;
    align-items:center;
    border-radius: 5px;
    transition:0.1s;
}

.stats__button:hover{
    transform:scale(0.9);
}

.stats__buttonText{
    font-size:1em;
}

@media (max-width: 679px) {
    .stats__button{
        margin-top:1em;
        margin-bottom:1em;
        height:40px;
    }
    .stats__buttonText{
        font-size:0.9em;
    }
}